import React from "react";
import { MODES } from "../data/Modes";
import { MAPS } from "../data/Maps";
import { PASSWORDS, PASSWORDS_ENUM } from "../data/Passwords";

const API = 'https://kyber.gg/api';
const API_SERVERS = `${API}/servers?page=`;
const API_PROXIES = `${API}/proxies`;
const API_CONFIG = `${API}/config`;
const API_CONFIG_PLAY = `${API_CONFIG}/play`;
const API_CONFIG_HOST = `${API_CONFIG}/host`;
const IMG_URL_PREFIX = "https://kyber.gg/static/images/maps/";
const IMG_URL_POSTFIX = ".jpg";

const MODS_ALL = 50;
const PLAYERS_MAX = 64;

class Form extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            pageCount: 0,
            serverCount: 0,

            mods: MODS_ALL,
            users: PLAYERS_MAX,
            maxPlayers: PLAYERS_MAX,
            passwordStatus: PASSWORDS_ENUM.ALL.value,

            passwords: PASSWORDS,
            modes: MODES,
            maps: MAPS,

            servers: [],
            proxies: []
        };

        this.onPasswordChange = this.onPasswordChange.bind(this);
        this.onModsChange = this.onModsChange.bind(this);
        this.onUsersChange = this.onUsersChange.bind(this);
        this.onMaxPlayersChange = this.onMaxPlayersChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount() {
        this.fetchProxies();
        this.fetchServers();
    }

    // PROXIES
    fetchProxies = async () => {
        await fetch(`${API_PROXIES}`)
            .then((response) => response.json())
            .then((data) => {
                this.setState({ proxies: data })
            })

        console.log(this.state.proxies);
    }

    // SERVERS
    fetchServers = async () => {
        this.setState({
            servers: []
        });
        await fetch(`${API_SERVERS}1`)
            .then((response) => response.json())
            .then((data) => {
                this.setState({
                    pageCount: data.pageCount,
                    serverCount: data.serverCount,
                    servers: this.state.servers.concat(data.servers)
                })
            });

        if (this.state.pageCount > 1) {
            for (var i = 2; i <= this.state.pageCount; i++) {
                await fetch(`${API_SERVERS}${i}`)
                    .then((response) => response.json())
                    .then((data) => {
                        this.setState({
                            servers: this.state.servers.concat(data.servers)
                        })
                    });
            }
        }

        console.log(`Found ${this.state.serverCount} Kyber servers: `);
        console.log(this.state.servers);
    }

    // WITH PASSWORD
    onPasswordChange(e) {
        this.setState({ passwordStatus: e.target.value });
    }

    // ACTIVE PLAYERS
    onUsersChange(e) {
        this.setState({ users: +e.target.value })
    }

    // SERVER CAPACITY
    onMaxPlayersChange(e) {
        let capacity = +e.target.value;

        this.setState({ maxPlayers: +e.target.value })
        if (this.state.users > capacity) {
            this.setState({ users: capacity })
        }
    }

    // MODS ENABLED
    onModsChange(e) {
        this.setState({ mods: +e.target.value })
    }

    // APPLY FILTERS
    applyFilters() {
        if (this.state.mods !== MODS_ALL) {
            let serverModsFilter = this.state.servers.filter(s => s.mods.length <= this.state.mods);
            this.setState({ servers: serverModsFilter });
        }

        if (this.state.maxPlayers !== PLAYERS_MAX) {
            let serverMaxPlayersFilter = this.state.servers.filter(s => s.maxPlayers <= this.state.maxPlayers);
            this.setState({ servers: serverMaxPlayersFilter });
        }

        if (this.state.users !== PLAYERS_MAX) {
            let serverPlayersFilter = this.state.servers.filter(s => s.users <= this.state.users);
            this.setState({ servers: serverPlayersFilter });
        }

        if (this.state.passwordStatus !== PASSWORDS_ENUM.ALL.value) {
            let requiresPassword = this.state.passwordStatus === PASSWORDS_ENUM.REQUIRED.value;
            var serverPasswordFilter = this.state.servers.filter(s => s.requiresPassword === requiresPassword)
            this.setState({ servers: serverPasswordFilter })
        }
    }

    // FORM SUBMIT
    handleSubmit = async (e) => {
        e.preventDefault();

        await this.fetchServers();

        this.applyFilters();

        console.log(`Filtered: `);
        console.log(this.state.servers);
    }

    render() {
        return (
            <div>
                <form onSubmit={this.handleSubmit}>
                    <div className="mods-wrapper">
                        <label htmlFor="mods">Mods (0 - 50): {this.state.mods === MODS_ALL ? "ALL" : this.state.mods === 0 ? "NO MODS" : this.state.mods}</label><br />
                        <input type="range" id="mods" name="mods" min="0" max={MODS_ALL} onChange={this.onModsChange} value={this.state.mods} />
                    </div>
                    <div className="maxPlayers-wrapper">
                        <label htmlFor="players">Max Server Capacity (2 - 64): {this.state.maxPlayers}</label><br />
                        <input type="range" id="maxPlayers" name="maxPlayers" min="2" max={PLAYERS_MAX} onChange={this.onMaxPlayersChange} value={this.state.maxPlayers} />
                    </div>
                    <div className="users-wrapper">
                        <label htmlFor="players">Active Players (1 - {this.state.maxPlayers}): {this.state.users}</label><br />
                        <input type="range" id="users" name="users" min="1" max={this.state.maxPlayers} onChange={this.onUsersChange} value={this.state.users} />
                    </div>
                    <div className="password-wrapper">
                        <label htmlFor="password">Password:</label>
                        <select value={this.state.passwordStatus} id="password" name="password" size="2" onChange={this.onPasswordChange} required>
                            {this.state.passwords.map((p) => (
                                <option key={p.value} value={p.value}>{p.name}</option>
                            ))}
                        </select>
                    </div>

                    <input className="submit-button" type="submit" value="Refresh" />
                </form>
                <ul>
                    {this.state.servers.map(s => {
                        const mapImage = IMG_URL_PREFIX + s.map.replaceAll('/', '-') + IMG_URL_POSTFIX;
                        const mapName = this.state.maps.find(m => m.map === s.map).name;
                        const modeName = this.state.modes.find(m => m.mode === s.mode).name.replaceAll('Versus', 'vs.');

                        return (
                            <li className="server">
                                <div>
                                    <img className="server-map-image" src={mapImage} alt={modeName + ' on ' + mapName} title={modeName + ' on ' + mapName} />
                                </div>
                                <div>
                                    <div>
                                        <span>{s.requiresPassword && "ðŸ”’ "}</span>
                                        <span className="uppercase">{s.name}</span>
                                    </div>
                                    <div style={{fontSize: "calc(6px + 2vmin)", color: "#fffc"}}>
                                        <span className="uppercase">{modeName}</span>
                                        {mapName && (
                                        <>
                                            <span> on </span>
                                            <span className="uppercase">{mapName}</span>
                                        </>
                                        )}
                                        {s.host && (
                                        <>
                                            <span> by </span>
                                            <span className="uppercase">{s.host}</span> 
                                        </>
                                        )}
                                        
                                    </div>
                                    <div style={{fontSize: "calc(6px + 2vmin)", color: "#fffc"}}>
                                        <span className="uppercase">{s.mods.length === 0 ? "no mods" : `mods installed: ${s.mods.length}`}</span>
                                    </div>
                                </div>
                                <div>
                                    <div>
                                        <span className="uppercase">{s.users}/{s.maxPlayers}</span>
                                    </div>
                                    <div >
                                        <progress style={{maxWidth: "75px"}} value={s.users} max={s.maxPlayers}></progress>
                                    </div>                                    
                                </div>
                                <div>
                                    <div>
                                        <span className="uppercase">
                                            <img src={s.proxy.flag} alt="location flag" className="server-flag-image" /> {s.proxy.name}
                                        </span>
                                    </div>                                    
                                    <div>
                                        <span className="uppercase">Started {s.startedAtPretty}</span>
                                    </div>
                                </div>
                                

                            </li>
                        )
                    }
                    )}
                </ul>
            </div>
        );
    }
}

export default Form;